/* Styling for custom select */
.customSelect {
  appearance: none; /* Remove default styling */
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: white;
  /* Make the select fill its container width */
  box-sizing: border-box;
  font-size: 14px;
  color: #333;
}

.customSelect:focus {
  outline: none;
  border-color: #007bff; /* Change border color on focus */
}

/* Styling for dropdown arrow icon */
.customSelect::-ms-expand {
  display: none; /* Remove the default arrow icon in Internet Explorer */
}

/* Position the dropdown arrow on the right side */
.customSelect:after {
  content: "▼"; /* Custom arrow icon */
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  pointer-events: none; /* Ensure the arrow doesn't interfere with clicking the select */
}

/* Adjust dropdown arrow color on focus */
.customSelect:focus:after {
  color: #007bff;
}
